.wrapper {
    margin: 32px 64px 0 64px;
}

.main {
    display: flex;
    position: relative;
}

@keyframes ArrowDown {
    0%,100% {
        transform: translateY(-24px);
    }
    50% {
        transform: translateY(0px);
    }
}

@keyframes ArrowDownMobile {
    0%,100% {
        transform: translateY(-12px);
    }
    50% {
        transform: translateY(0px);
    }
}

.frame {
    width: 50%;
    display: flex;
    justify-content: center;
}

.frame p {
    position: absolute;
    right: 0;
    display: flex;
    gap: 8px;
    bottom: 8px;
    font-size: 40px;
    letter-spacing: 0.6px;
}

.frame p span:last-child {
    align-self: end;
    display: flex;
    gap: 10px;
}

.frame p span i {
    font-style: normal;
    font-family: Ancient;
    transform: scale(1.5);
    margin-top: -4px;
}

.frameArrow {
    margin-top: 16px;
    transform: rotate(0.5deg);
}

.frameArrow svg {
    height: 100%;
    width: 100%;
    animation-name: ArrowDown;
    animation-timing-function: linear;
    animation-duration: 6s;
    animation-iteration-count: infinite;
}

.description {
    padding: 20px 0px;
    padding-left: 96px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.title {
    color: var(--gold);
    font-size: 80px;
    font-family: Ancient;
    font-weight: 600;
    letter-spacing: 2.5px;
    line-height: 1.1;
    text-align: center;
}

.button {
    margin-top: 96px;
    padding: 10px 20px;
    cursor: pointer;
    position: relative;
    padding-left: 40px;
    transition: all 0.4s ease;
    background: transparent;
    font-family: Ancient;
    font-weight: 400;
    font-size: 36px;
    letter-spacing: -0.01em;
    border: 2px solid var(--gold);
    color: var(--gold);
    -webkit-text-stroke-width: 0.4px;
}

.border {
    display: block;
    width: 100%;
    height: 4px;
    margin-bottom: 7px;
    background: var(--gold);
}

.button>i {
    font-style: normal;
    font-family: London;
    position: absolute;
    top: 6px;
    left: 8px;
    transition: color 0.1s ease, transform 0.3s ease;
}
.frame p {
    position: absolute;
}

@media (hover:hover) {
    .button:hover i {
        font-style: normal;
        transform: rotate(-90deg) translate(-6px, 2px);
    }

    .button:hover {
        color: var(--silver);
        border: 2px solid var(--silver);
        border-radius: 32px;
    }
}

@media (max-width: 1420px) {
    .wrapper {
        margin: 32px 40px 0 40px;
    }

    .description {
        margin-top: 8px;
        padding: 0px;
    }

    .title {
        font-size: 72px;
    }

    .button {
        margin-top: 40px;
    }

    .frame p {
        flex-direction: column;
    }

    .frame p span:first-child {
        margin-left: -32px;
    }
}

@media (max-width: 1280px) {
    .title {
        font-size: 64px;
    }

    .button {
        margin-bottom: 24px;
    }
}

@media (max-width: 1080px) {
    .main {
        gap: 24px;
        padding-bottom: 40px;
    }

    .frame { 
        width: 40%;
    }

    .description {
        margin-top: 0;
    }
}

@media (max-width: 970px) {
    .main {
        padding-bottom: 140px;
    }

    .frame { 
        width: 50%;
    }
}

@media (max-width: 940px) {
    .title {
        font-size: 56px;
    }
}

@media (max-width: 860px) {
    .main {
        flex-direction: column;
        align-items: center;
        position: static;
        padding-bottom: 240px;
    }

    .frame p {
        bottom: -88px;
        right: 40px;
    }

    .frame p span:first-child {
        margin-right: 24px;
    }

    .frameArrow {
        position: absolute;
        top: 6px;
        left: 14px;
    }

    .frameArrow svg {
        height: 160px;
        animation-name: ArrowDownMobile;
        animation-duration: 7s;
        object-fit: contain;
    }
}

@media (max-width: 640px) {
    .frameArrow svg {
        height: 140px;
    }

    .frame p {
        bottom: -80px;
    }
}

@media (max-width: 623px) {
    .frameArrow {
        left: 50%;
        top: 160px;
        transform: translateX(-50%);
    }

    .frameArrow svg {
        height: 124px;
    }

    .title {
        margin-top: 96px;
    }

    .frame p {
        bottom: -240px;
    }
}

@media (max-width: 550px) {
    .wrapper {
        margin: 8px 16px 0 16px;
    }
}



@media (max-width: 560px) {
    .frame p {
        font-size: 32px;
    }
}

@media (max-width: 420px) {
    .title {
        font-size: 40px;
    }

    .frame p {
        font-size: 30px;
        flex-direction: column;
        gap: 4px;
        bottom: 120px;
    }

    .frame p span:nth-child(2) {
        margin-left: 32px;
    }

    .button {
        margin-top: 16px;
        padding: 8px 16px;
        padding-left: 32px;
    }
}

@media (max-width: 382px) {
    .frame p {
        bottom: -80px;
        right: 16px;
    }
    .frame p span:first-child {
        margin: 0;
    }
}

@media (max-width: 300px) {
    .title {
        font-size: 32px;
    }
}